import React from "react";
import "./Home.css";
import { Link } from "react-router-dom";
import  background  from "../images/pak.jpg";
import logo from "../images/airbnb.png";
import { ConnectButton , Select , DatePicker ,Input,Icon , Button } from "web3uikit";
import { useState } from "react";


const Home = () => {

  const[checkIn, setCheckIn] = useState(new Date());
  const[checkOut, setCheckOut] = useState(new Date());
  const[destination , setDestination] = useState("Islamabad");
  const[guests, setGuests] = useState(2);

  return (
    <>
      <div className="container" style={{ backgroundImage: `url(${background})`
    }} >
        <div className="containerGradient"></div>
      </div>
        <div className="topBanner">
          <div>
            <img className="logo" src={logo}></img>
          </div>
          <div className="tabs">
            <div className="selected">Places To Stay</div>
            <div>Experiances</div>
            <div>Online Experiances</div>
          </div>
          <div className="lrContainers">
          <ConnectButton />
          </div>
        </div>
        <div className="tabContent">
          <div className="searchFields">

            <div className="inputs">
              Location
              <Select defaultOptionIndex={0} 
                      onChange={(data) => setDestination(data.label)}
                      options={[
                        {
                          id: "isb",
                          label: "Islamabad"
                        },
                        {
                          id: "lhr",
                          label: "Lahore"
                        },
                        {
                          id: "khi",
                          label: "Karachi"
                        },
                        {
                          id: "pew",
                          label: "Peshawar"
                        }
                      ]}
                      />
            </div>

            <div className="vl" />
            <div className="inputs">
              Check In
              <DatePicker
                id="CheckIn"
                onChange={(event)=> setCheckIn(event.date)} 
                />
            </div>

            <div className="vl" />
            <div className="inputs">
              Check Out
              <DatePicker
                id="CheckIn"
                onChange={(event)=> setCheckOut(event.date)} 
                />

            </div>

            <div className="vl" />
            <div className="inputs">
              Guests
              <Input 
                value={2}
                name="AddGuests"
                type="number"
                onChange={(event)=> setGuests(Number(event.target.value))}
                />
            </div>
                    
            <Link to={"/rentals"} state=
            {{
              destination: destination,
              checkIn: checkIn,
              checkOut: checkOut,
              guests: guests
            
            }} >       
            <div className="searchButton">
              <Icon
                fill="#fff"
                size={24}
                svg="search"
              />
            </div>
            </Link> 
          </div>
        </div>  

        <div className="randomLocation">
        <div className="title">Feel Adventurous</div>
        <div className="text">
          Let us decide and discover new places to stay, live, work or just
          relax.
        </div>
        <Button
          text="Explore A Location"
          onClick={() => console.log(checkOut)}
        />
      </div>
    </>
  );
};

export default Home;
